@import url('https://fonts.googleapis.com/css2?family=Mochiy+Pop+One&display=swap');

body{
    background-color: #224870;
    color: #fff;
    width: 100%;
    font-family: 'Mochiy Pop One', sans-serif;
}

.container{

}

h1{
    color: #fff;
    margin: 0;
    padding: 0;   
    font-family: 'Mochiy Pop One', sans-serif;
}

p{
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none;
    color: inherit;
    transition: all .2s;
  }

a:hover{
    color: #02C39A;
}


span{
    margin: 0;
    padding: 0;    
}

hr{
    border-color: #2A4494;
}

button{
    border-radius: 1rem;
    border: 1px solid #fff;
    background-color: #fff;
    width: 300px;
    padding: 1rem;
    font-size: 1rem;
    text-transform: uppercase;
    font-weight: 700;
    transition: all .3s;
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2), 0 6px 20px 0 rgba(0,0,0,0.19);
}

button:hover{
    background-color: #00000000;
    color: #fff;
    cursor: pointer;
}

img{
    filter: grayscale(50%);
    transition: all .1s;
    border-top-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    border: 3px solid #00A896;
    outline: 3px solid #02C39A;
    outline-offset: -6px;
    max-width: 110px;

}


img:hover{
    filter: grayscale(0);
    transform: scale(1.05);
    cursor: pointer;
}

/* *** flexbox *** */

.flex{
    display: flex;
}

.flex-col{
    display: flex;
    flex-direction: column;
}

.flex-wrap{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: min(3rem, 5vh);
    max-width: 800px;
}

/* *** grid *** */
.grid{
    display: grid;
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: repeat(5, 1fr);
    gap: min(3rem, 5vh);
}


/* ** Header ** */

.header{
    justify-content: space-around;
    flex-wrap: wrap;
}

.score{
    gap: 2rem;
}

.score > h2 > span{
    background-color: #2A4494;
    padding: .7rem;
    margin: .4rem;
    border-radius: 1rem;
}

/* ** MAIN ** */

.main{
    align-items: center;
    margin-top: 5rem;
    margin-bottom: 5rem;
}


.game-intro, .game-over{
    max-width: 450px;
    padding: 3rem;
    text-align: center;
    background-color: #122C34;
    border-radius: 2rem;
    text-align: center;
}



/* ** footer ** */
.footer{
    margin-top: 20px;
    text-align: center;
    background-color: #00000053;
}

.footer p {
    padding: 1rem;
}


/* ** Card ** */

.card{
    position: relative; 
}

.card:hover .imgInfo{
    transform: scale(1.05);
}

.imgInfo{
    position: absolute;
    bottom:25%;
    left: 6px;
    width: 103px;
    background-color: #00000080;
    padding-top: .3rem;
    padding-bottom: .3rem;
    cursor: pointer;
}

.imgInfo p {
    text-align: center;
    font-size: .5rem;
}